@import url(https://fonts.googleapis.com/css?family=Poppins:400,500,600|Titillium+Web:400,600);
:root {
  --primary: #00a9d6;
}

.startforfree-btn {
  padding: 8px 2px;
  border-radius: 10px;
  width: 135px;
  outline: none;
  border: none;
  font-size: 18px;
  color: #fff;
  cursor: pointer;
  background-color: #00a9d6;
  background-color: var(--primary);
}

.login-btn {
  padding: 8px 20px; 
  border-radius: 10px;
  outline: none;
